body
{
    background-color: rgb(48, 34, 56);
}

footer
{
    
    left: 0;
    right: 0;
    bottom: 0;
    background-image: url("https://images.unsplash.com/photo-1618386230353-3631c1365be2?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=876&q=80");
    color: aliceblue;
}

#controller
{
    height: 300px;
    width: 300px;
}

#pSearch
{
    position: relative;
}

#card
{
    width: 300px;
    position: absolute;
    background-color: aliceblue;
    left: 10px;
    color: gray;
}

.prod:hover
{
    background-color: rgba(128, 128, 128, 0.384);
}

.prod
{
    cursor: pointer;
}

.pr
{
    color: rgb(82, 82, 82);
}
.bgbutton
{
    background-color: blueviolet;
    color:antiquewhite;
}

#subpc
{
    color:rgb(180, 146, 212);
}

th, td
{
    color: white;
}

.fb
{
    width: 20px;
    height: 20px;
}

#subs
{
    background-color: blueviolet;
    color: whitesmoke;
}

a
{
    text-decoration: none;
}
.header-right > a
{
    color: whitesmoke;
    text-align: center;
    text-decoration: none;
    font-size: 18px;
    line-height: 25px;
    border-radius: 4px;
}

.header-right > p
{
    color: whitesmoke;
    text-align: center;
    text-decoration: none;
    font-size: 18px;
    line-height: 25px;
    border-radius: 4px;
    padding: 12px;
    height: 30px;
}

.letras
{
    padding: 10px;
}
.header-right input
{
    padding: 12px;
    text-decoration: none;
    font-size: 18px;
    line-height: 25px;
    border-radius: 4px;
}

.header-right > a:hover
{
    background-color: rgb(194, 138, 216);
    color: rgb(0, 0, 0);
}

#searchInput
{
    height: 25px;
    width: 300px;
}

.links
{
    padding: 10px;
}

.header-right img
{
    padding: 12px;
    line-height: 25px;
    border-radius: 4px;
    height: 50px;
}

hr
{
    padding: 0;
    margin: 0;
}